import Noticia from '../components/Noticia';

const Noticias = (props) => {
    return (
        <div className="row">
            {props.noticias.map((noticia, idx) => (
                <Noticia
                    key = {idx}
                    noticia={noticia}
                />
            ))}
        </div>
    );
}

export default Noticias;